//** Width of the `border` for generating carets that indicate dropdowns.
@plone-caret-width-base:          4px;
//** Carets increase slightly in size for larger components.
@plone-caret-width-large:         5px;
@plone-font-size-base:           16px;

/*General Menu Styling*/
.plone-nav {
  margin: 0 auto;


  // li {
  //   list-style: none;
  // }

  // li a {
  //   text-decoration: none;
  // }

  .dropdown {
    position: absolute;
    // width: 150px;
    // top: 41px;
    padding: 0;
    background: @plone-link-color;
    opacity: 0;
    visibility: hidden;
    transition: ease-out 0.75s;
    -moz-transition: ease-out 0.75s;
    -webkit-transition: ease-out 0.75s;
    li {
      position: relative;
      list-style: none;
      padding: 0;
      a {
        color: white;
        min-width: 14em;
        padding: 10px 15px 10px 15px;
        display: inline-block;
        &:hover {
          background: @plone-sitenav-link-hover-bg;
        }
        // ul {
        //   left: 14em;
        // }
      }
    }
  }

  .plone-nav li {
    // float: left;
    // padding: 5px;
    // background: maroon;
    // border-left: 1px dotted #fff;
  }

  .plone-nav li:first-child {
    // border: none;
  }

  .plone-nav li a {
    // display: block;
    // padding: 2px 20px;
    // color: #fff;
    // font-family: arial;
  }

  .plone-nav li:hover {
    // background: #fff;
    transition: ease-in .35s;
    -moz-transition: ease-in .35s;
    -webkit-transition: ease-in .35s;
  }

  .plone-nav li:hover a {
    // color: maroon;
    transition: ease-in .35s;
    -moz-transition: ease-in .35s;
    -webkit-transition: ease-in .35s;
  }


  /*First Level*/
  li {
    // left: -45px;
    // position: relative;
    // top: 0px;
    // width: 175px;
    // border-left: none !important;
    // border-bottom: 1px dotted #fff !important;
  }

  li:last-child {
    // border: none !important;
  }

  .has_subtree:hover > .dropdown,
  .has_subtree .has_subtree:hover > .dropdown {
    opacity: 1;
    visibility: visible;
    transition: ease-in .35s;
    -moz-transition: ease-in .35s;
    -webkit-transition: ease-in .35s;
  }

  .plone-nav li:hover ul a,
  .plone-nav li:hover ul li ul li a {
    color: white;
  }

  .plone-nav li ul li:hover,
  .plone-nav li ul li ul li:hover {
    // background: #fff;
    transition: ease-in-out .35s;
    -moz-transition: ease-in-out .35s;
    -webkit-transition: ease-in-out .35s;
  }

  .plone-nav li ul li:hover a,
  .plone-nav li ul li ul li:hover a {
    // color: maroon;
    transition: ease-in-out .35s;
    -moz-transition: ease-in-out .35s;
    -webkit-transition: ease-in-out .35s;
  }

  /*Second Level*/
  .has_subtree .has_subtree .dropdown li {
    // left: 25px;
    position: relative;
    // outline: 1px dotted red;
    // width: 165px;
    top: 0px;
  }

  .has_subtree .has_subtree .dropdown {
    // outline: 1px dotted red;
    position: absolute;
  //   width: 150px;
  //   left: 120px;
    left: 14em;
    top: 0px;
  //   opacity: 0;
  //   visibility: hidden;
  //   transition: ease-out 0.75s;
  //   -moz-transition: ease-out 0.75s;
  //   -webkit-transition: ease-out 0.75s;
  }
}
